<template>
	<div>
		<el-row>
			<headers :title='title'></headers>
		</el-row>
		<el-row>
			<el-col :span='18' :offset='3'>
				<div class="news_d">
					<div class="news_d_t">
						<el-row>
							<el-col :span='18'>
								<div class='news_d_title'>
									<span class="t">{{arr.title}}</span>
									<span class="s"><span>{{arr.userName}}</span>  {{arr.creationTime}} 浏览数(1000)</span>
								</div>
							</el-col>
							<el-col :span='6'>
								<div class="news_d_r">
									<!-- <el-button @click='ret(arr.id)'>撤回</el-button>
									<el-button @click='del(arr.id)'>删除</el-button>
									<el-button @click='edit'>编辑</el-button> -->
								</div>
							</el-col>
						</el-row>
					</div>
					<div class="news_d_c" >
						<div v-html="arr.particulars">
							{{arr.particulars}}
						</div>
						<div>
							<img :src="arr.photo" alt="">
						</div>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import headers from '../../components/header.vue'
	export default{
		components:{
			headers
		},
		data(){
			return{
				title:'新闻详情',
				arr:[]
			}
		},
		methods:{
			/*编辑*/
			edit(){
				this.$router.push('/home/news_edit')
			},
			/*删除*/
			del(id){
				this.$confirm('是否删除?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then((action) => {
					if (action === 'confirm') {
							this.$axios.post('/tomlive/pressCenter/deletePressCenterByPrimaryKey', this.$qs.stringify({
								id: id
							})).then((res) => {
								console.log(res)
								this.$router.push('/home/news_manage')
						})
					}}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消'
					});
				})
				console.log(id)
			},
			/*撤回*/
			ret(id){
				this.$confirm('是否撤回?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then((action) => {
					if (action === 'confirm') {
							this.$axios.post('/tomlive/pressCenter/updatePressCenterStatus', this.$qs.stringify({
								id: id
							})).then((res) => {
								console.log(res)
								this.$router.push('/home/news_manage')
						})
					}}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消'
					});
				})
				console.log(id)
			}
		},
		mounted(){
			var arr=window.sessionStorage.getItem('news_d')
			this.arr=JSON.parse(arr)
		}
	}
</script>

<style scoped>
	.news_d{
		width: 100%;
		min-height: 600px;
		border: 1px solid #e9e9e9;
		background: #fff;
		box-sizing: border-box;
		margin: 20px 0;
	}
	.news_d_t{
		width: 100%;
		height: 110px;
		background: #f5f5f5;
		border-bottom: 1px solid #e9e9e9;
		box-sizing: border-box;
	}
	.news_d_title{
		width: 100%;
		padding: 20px;
	}
	.news_d_title>.t{
		display: block;
		font-size: 20px;
		font-weight: bold;
		color: #666;
	}
	.news_d_title>.s{
		font-size: 14px;
		color: #999;
		line-height: 45px;
	}
	.news_d_r{
		width: 100%;
		padding: 40px 70px;
	}
	.news_d_c{
		padding: 20px;
		font-size: 14px;
		color: #666;
		line-height: 30px;
		text-indent: 2em;
	}
	.news_d_c>div{
		margin: 20px auto;
	}
</style>